/* .message__blobSvg > g > text { */
foreignObject {
  padding: 10px;
}
.message__blobText {
  text-anchor: middle;
  text-align: center;
  white-space: pre-wrap !important;
  z-index: 2;
  display: flex;
  position: relative;
  top: 22%;
  justify-content: center;
  -webkit-animation: textShadow 1.2s infinite ease-in-out paused,
    floating 3s infinite ease-in-out running;
          animation: textShadow 1.2s infinite ease-in-out paused,
    floating 3s infinite ease-in-out running;
  font-size: 13px !important;
}
/* .message__blobSvg:hover > g > text { */
.message__blobSvg:hover .message__blobText {
  -webkit-animation-play-state: running, paused;
          animation-play-state: running, paused;
}

@-webkit-keyframes floating {
  0% {
    transform: translateY(50%);
  }
  50% {
    transform: translateY(20%);
  }
  100% {
    transform: translateY(50%);
  }
}

@keyframes floating {
  0% {
    transform: translateY(50%);
  }
  50% {
    transform: translateY(20%);
  }
  100% {
    transform: translateY(50%);
  }
}

@-webkit-keyframes textShadow {
  0% {
    transform: translateY(50%);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 #fb203b, 0 0 0 #fefc4b;
  }

  25% {
    transform: translateY(42%);
    text-shadow: 0 0.125em 0 #0c2ffb, 0 0.25em 0 #2cfcfd, 0 -0.125em 0 #fb203b,
      0 -0.25em 0 #fefc4b;
  }

  50% {
    transform: translateY(40%);
    text-shadow: 0 -0.0625em 0 #0c2ffb, 0 -0.125em 0 #2cfcfd,
      0 0.0625em 0 #fb203b, 0 0.125em 0 #fefc4b;
  }

  75% {
    transform: translateY(42%);
    text-shadow: 0 0.03125em 0 #0c2ffb, 0 0.0625em 0 #2cfcfd,
      0 -0.03125em 0 #fb203b, 0 -0.0625em 0 #fefc4b;
  }

  100% {
    transform: translateY(50%);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 #fb203b, 0 0 0 #fefc4b;
  }
}

@keyframes textShadow {
  0% {
    transform: translateY(50%);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 #fb203b, 0 0 0 #fefc4b;
  }

  25% {
    transform: translateY(42%);
    text-shadow: 0 0.125em 0 #0c2ffb, 0 0.25em 0 #2cfcfd, 0 -0.125em 0 #fb203b,
      0 -0.25em 0 #fefc4b;
  }

  50% {
    transform: translateY(40%);
    text-shadow: 0 -0.0625em 0 #0c2ffb, 0 -0.125em 0 #2cfcfd,
      0 0.0625em 0 #fb203b, 0 0.125em 0 #fefc4b;
  }

  75% {
    transform: translateY(42%);
    text-shadow: 0 0.03125em 0 #0c2ffb, 0 0.0625em 0 #2cfcfd,
      0 -0.03125em 0 #fb203b, 0 -0.0625em 0 #fefc4b;
  }

  100% {
    transform: translateY(50%);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 #fb203b, 0 0 0 #fefc4b;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important;
  }
}

